import React, { Component } from 'react'
import { Button, Input } from 'antd';
import { history } from '@/utils/history'

const Footlogin_box = (props) => {
  const {
    input_accfn, input_acc
  } = props
  return (
    <div className="footlogin_box">
      <div className="footlogin_box_warp">
        <div className="tiole">
          开始您的交易
        </div>
        <div className="min_liang">
          仅需要2分钟，您就可以开始自由地交易比特币和数百种资产
        </div>
        <div className="input_box">
          <Input placeholder="您的手机号或邮箱地址" onChange={input_accfn} style={{ width: 540, height: 60 }}></Input>
          <Button onClick={() => {
            if (input_acc) {
              history.push('/register?account=' + input_acc)

            } else {
              history.push('/register')
            }
          }} type="primary" style={{ width: 150, height: 60, marginLeft: 20 }}>创建账户</Button>
        </div>
      </div>
    </div>
  )
}
export default Footlogin_box