import React, { Component } from 'react'
import { FormattedMessage } from 'react-intl';
const Exhibition_platform = (props) => {
  const {
    data
  } = props
  return (
    <div className="cow">
      <div className="span-tit" >
        {data.title}
      </div>
      <div className="span-cos-1">
        {
          data.content
        }
      </div>
    </div>
  )
}
export default Exhibition_platform